import React, { Component } from 'react';
import './styles.css';
import PlayerCard from '../../components/playerCard/playerCard.js';

class PlayersList extends Component {
  
  render() {
    const { quantity, playersData } = this.props;
    return (
      <div className="playersList row col-12 px-0 mx-0">
        { playersData.length
          ? (playersData.slice(0, quantity)).map((player)=> (
              <div className="photoItem px-0 d-flex align-items-center" key={player.id} id={player.id}>
                <PlayerCard
                  data={player}
                />
              </div>
            ))
          : <div className="not">
              <span>нет игроков</span>
            </div>
        }
      </div>
    )
  }
}

export default PlayersList;